.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 4;
  padding-top: 30px;

}

.menu {
  margin-left: auto;
  margin-right: auto;
  &__item {
    &:not(:last-child) {
      margin-right: 27px;
    }
  }

  &__link {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 0.07em;
    color: var(--white);
    transition: color .3s ease-in-out;
    path {
      stroke: currentColor;
      fill: currentColor;
    }
    &--active {
      color: var(--accent);

    }

    &:hover {
      color: var(--accent);
    }

    span {
      padding-left: 5px;
    }
  }
}

.box-burger {
  width: 45px;
  height: 45px;
  background-color: #292929;
}

.burger {
  pointer-events: none;
  @include burger;
  color: var(--white);
  width: 18px;
  height: 12px;

  &__line {
  }
}
