.portfolio {
  padding-top: 20px;
  padding-bottom: 118px;

  &__inner {
    position: relative;
    gap: 28px;

    &::before {
      position: absolute;
      content: "";
      bottom: -63px;
      left: -47px;
      width: 72px;
      height: 68px;
      background-image: url("../img/shape-three.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }
}

.portfolio-content {
  max-width: 472px;

  &__title {
    margin-bottom: 21px;
  }

  &__text {
    position: relative;
    margin-bottom: 50px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 144%;
    color: var(--text);
  }

  &__link {
    margin-left: 41px;
  }
}

.accardeon {
  max-width: 872px;
  width: 100%;
  padding-top: 20px;

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-bottom: 56px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    }

    // &::before {
    //   position: absolute;
    //   content:'';
    //   top: 50%;
    //   transform: translateY(-50%);
    //   right: 30px;
    //   background-image: url('../img/portfolio-img-1.jpg');
    //   width: 250px;
    //   height: 280px;
    //   background-repeat: no-repeat;
    //   background-size: cover;
    // }
  }

  &__control {
    width: 100%;
  }

  &__num {
    position: relative;
    display: inline-block;
    margin-right: 30px;
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 60px;
    line-height: 1;
    transform: translateY(-22px);
    background: linear-gradient(
      180deg,
      rgba(255, 240, 240, 1) 0%,
      rgba(0, 0, 0, 1) 100%
    );
    -webkit-background-clip: text;
    -webkit-text-stroke: 4px transparent;
    color: var(--bg);

    &::before {
      position: absolute;
      content: "/";
      width: 15px;
      bottom: -20px;
      right: -15px;
      font-family: var(--second-family);
      font-weight: 600;
      font-size: 25px;
      line-height: 140%;
      transform: translateY(-22px);

      color: inherit;

      background: linear-gradient(
        180deg,
        rgba(255, 240, 240, 1) 0%,
        rgba(0, 0, 0, 1) 100%
      );
      -webkit-background-clip: text;
      -webkit-text-stroke: 2px transparent;
    }
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 30px;
    line-height: 140%;
    color: var(--white);

    span {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 15px;
    }
  }

  &__box {
    position: relative;
    display: inline-block;
    margin-left: auto;
    width: 20px;
    height: 20px;

    &::before,
    &::after {
      position: absolute;
      content: "";
      background-color: var(--white);
      transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
    }

    &::before {
      width: 20px;
      height: 2px;
      top: 50%;
      right: 4px;
      transform: translateY(-50%);
    }

    &::after {
      height: 20px;
      width: 2px;
      transform-origin: center;
      top: 50%;
      transform: translateY(-50%);
      left: 5px;
    }
  }

  &__content {
    position: relative;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: var(--gray);

    max-height: 0;
    will-change: max-height;
    overflow: hidden;
    transition: 0.3s linear;
    box-sizing: content-box;
  }

  &__img {
    position: absolute;
    max-width: 250px;
    max-height: 280px;
    object-fit: cover;
    // top: -37px;
    // right: -24px;
    top: -77px;
    right: 22px;
    transform: rotate(15deg);
    z-index: -1;
    opacity: 0;
    transition: opacity 0.1s linear, right 0.3s linear, top 0.3s linear;
  }
}

.accardeon__item--open .accardeon__box::before {
  transform: rotate(180deg);
}

.accardeon__item--open .accardeon__box::after {
  opacity: 0;
}

.accardeon__item--open .accardeon__content {
  padding: 0 0 23px;
  opacity: 1;
}

.accardeon__item--open .accardeon__img {
  top: -37px;
  right: -24px;
  opacity: 1;
  transition: opacity 1s linear 0.3s, right 0.6s linear 0.3s,
    top 0.6s linear 0.3s;
}
