.features {
  padding-bottom: 210px;

  &__top {
    position: relative;
    margin-bottom: 63px;

    &::before {
      position: absolute;
      content: "";
      bottom: -91px;
      right: -50px;
      background-image: url("../img/shape-two.svg");
      width: 106px;
      height: 76px;
    }
  }

  &__top-left {
    max-width: 460px;
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 40px;
    line-height: 135%;
    color: #fff;
  }

  &__right {
    max-width: 470px;
  }

  &__text {
    display: block;
    margin-bottom: 39px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 144%;
    color: #aaa;
  }

  &__link {
    margin-left: 35px;
  }
}

.steps-list {
  gap: 60px;

  &__item {
    &:nth-child(2) {
      transform: translateY(60px);
    }

    &:nth-child(3) {
      transform: translateY(120px);
    }
  }

  &__link {
    &:hover {
      .steps-list__image {
        filter: grayscale(0);

        &::before {
          opacity: 0.7;
          transition: opacity 0.4s linear 0.1s;
        }
      }

      .steps-list__title {
        color: var(--accent);
      }
    }
  }

  &__image {
    position: relative;
    width: 350px;
    height: 380px;
    margin-bottom: 18px;
    transition: filter 0.2s linear;
    filter: grayscale(1);

    &::before {
      position: absolute;
      content: "";
      bottom: 0;
      right: 0;
      width: 70px;
      height: 70px;
      background-color: var(--accent);
      background-image: url("../img/arrow-link.svg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: auto;
      opacity: 0;
      transition: opacity 0.2s linear 0s;
    }
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 30px;
    line-height: 135%;
    color: var(--white);
    transition: color 0.3s ease-in-out;
  }
}
