.partners {
  padding-top: 167px;
  padding-bottom: 89px;

  &__inner {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: -113px;
      left: -35px;
      width: 72px;
      height: 68px;
      background-image: url("../img/shape-three.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }

  &__text {
    position: relative;
    max-width: 350px;
    padding-left: 17px;
    margin-right: 51px;

    font-family: var(--font-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.15em;
    color: var(--gray);

    &::before {
      position: absolute;
      content: "";
      top: 50%;
      transform: translateY(-50%);
      left: 1px;
      width: 3px;
      height: 83%;
      background-color: var(--accent);
    }
  }
}

.partners-list {
  &__item {
    max-height: 60px;

    &:not(:last-child) {
      margin-right: 77px;
    }
  }

  &__item-img {
  }
}
