.blog {
  padding-bottom: 75px;
  &__top {
    margin-bottom: 62px;
  }

  &__top-left {
    width: 437px;
  }

  &__top-right {
    width: 470px;
  }

  &__top-descr {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 144%;
    color: var(--text);
    margin-bottom: 38px;
  }

  &__top-link {
    margin-left: 37px;
  }
}

.blog-list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;

  &__item {
    grid-column: 6 span;
  }
}

.blog-post {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 3;

  &__image {
    position: relative;
    max-width: 570px;
    height: 352px;
    clip-path: polygon(0 0,100% 0,100% 77%,18% 77%,18% 100%,0 100%);
    z-index: 1;
  }

  &__img {
    width: 100%;
    height: 100%;
    filter: grayscale(1);
  }

  &__content {
    order: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-top: -81px;
    max-width: 470px;
    padding: 17px;
    z-index: 2;
  }

  &__time {
    margin-bottom: 7px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 13px;
    line-height: 140%;
    color: var(--gray);
  }

  &__title {
    width: 344px;
    margin-bottom: 22px;
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 30px;
    line-height: 135%;
    color: var(--white);
  }

  &__text {
    margin-bottom: 21px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: var(--text);
  }

  &__link {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    letter-spacing: 0.25em;
    color: var(--white);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--accent);

      svg * {
        stroke: currentColor;
      }
    }

    span {
      padding-left: 10px;

      svg * {
        transition: stroke 0.3s ease-in-out;
      }
    }
  }
}
