.contact {
  padding-bottom: 103px;

  &__top {
    margin-bottom: 94px;
  }

  &__title {
    width: 668px;
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 60px;
    line-height: 135%;
    color: var(--white);
  }

  &__image {
    max-width: 570px;
    filter: grayscale(1);

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.with-us {
  width: 250px;
  height: 250px;
  margin-right: 53px;
  border-radius: 100%;
  background-color: #333;
  color: var(--white);
  transition: all 0.3s ease-in-out;

  &:hover {
    color: var(--accent);
  }

  svg {
    path {
      fill: currentColor;
      stroke: currentColor;
    }
  }

  &__link {
    padding-top: 42px;
    margin-bottom: 16px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 0.18em;
    color: currentColor;
  }
}

.contact-content {
  position: relative;
  width: 472px;

  &::before {
    position: absolute;
    content: "";
    background-image: url("../img/shape-portf.svg");
    bottom: -63px;
    right: -34px;
    width: 72px;
    height: 72px;
    background-repeat: no-repeat;
  }

  &__title {
    margin-bottom: 26px;
  }

  &__text {
    margin-bottom: 50px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 144%;
    color: var(--text);
  }

  &__link {
    margin-left: 38px;
  }
}
