.footer {
  padding-top: 102px;

  &__inner {
    gap: 30px;
    padding-bottom: 51px;
    border-bottom: 1px solid var(--gray);
  }

  &__link {
    display: inline-block;
    margin-bottom: 22px;
  }

  &__logo {
  }

  &__text {
    max-width: 270px;
    margin-bottom: 30px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 15px;
    line-height: 140%;
    color: var(--text);
  }

  &__tel {
    position: relative;
    display: inline-block;
    padding: 8px 16px 8px 40px;
    background-color: var(--accent);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 13px;
    line-height: 182%;
    color: var(--white);

    &::before {
      position: absolute;
      content: "";
      top: 50%;
      left: 16px;
      transform: translateY(-50%);
      background-image: url("../img/tel.svg");
      width: 15px;
      height: 15px;
      background-repeat: no-repeat;
    }
  }

  &__item-title {
    margin-bottom: 27px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 140%;
    text-transform: uppercase;
    color: var(--white);
  }

  &__list {
    gap: 30px;
  }

  &__social-descr {
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 30px;
    line-height: 140%;
    color: var(--white);

    a {
      text-decoration: underline;
      transition: color 0.3s ease-in-out;

      &:hover {
        color: var(--accent);
      }
    }
  }

  &__bottom {
    padding: 25px 0 29px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 15px;
    line-height: 140%;
    color: var(--gray);
  }

  &__bottom-links {
  }

  &__bottom-link {
    display: inline-block;

    &:not(:last-child) {
      margin-right: 52px;
    }
  }
}

.footer-list {
  &__item-link {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 15px;
    line-height: 182%;
    color: var(--text);
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--white);
    }
  }
}

.social {
  margin-bottom: 30px;
  padding-top: 60px;

  &__item {
    position: relative;
    &:not(:last-child) {
      padding-right: 11px;
      margin-right: 10px;

      &::before {
        position: absolute;
        content: "";
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        width: 1px;
        height: 18px;
        background-color: var(--white);
        opacity: 0.3;
        background-repeat: no-repeat;
      }
    }
  }

  &__link {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
  }

  &__link--fb {
    background: linear-gradient(180deg, #0052ff 0%, #fff 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__link--db {
    background: linear-gradient(180deg, #ea4c89 0%, #fff 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__link--tw {
    background: linear-gradient(
      180deg,
      #09bbff 0%,
      rgba(255, 255, 255, 0.96) 100%
    );
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &__link--inst {
    background: linear-gradient(
      180deg,
      #565bc9 0%,
      #cc3385 59.37%,
      #f69e54 100%
    );
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
