.about {
  padding-bottom: 210px;

  &__left {
    position: relative;
    max-width: 670px;
    width: 100%;
    margin-right: 32px;

    &::before,
    &::after {
      position: absolute;
      content: "";
      background-repeat: no-repeat;
      background-size: contain;
      z-index: -1;
    }

    &::before {
      top: 55px;
      right: 0;
      background-image: url("../img/about-decor-img-1.svg");
      width: 150px;
      height: 101px;
    }

    &::after {
      bottom: -115px;
      right: 284px;
      background-image: url("../img/about-decor-img-circle.svg");
      width: 200px;
      height: 200px;
    }
  }

  &__image {
    width: 570px;
    height: 600px;
    clip-path: polygon(0 0, 100% 0, 100% 41%, 47% 41%, 47% 100%, 0 100%);
  }

  &__img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }

  &__video {
    position: absolute;
    width: 390px;
    height: 440px;
    overflow: hidden;
    bottom: -62px;
    right: -1px;

    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__video-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100px;
    height: 100px;
    background-color: var(--accent);
    border-radius: 100%;
    z-index: 1;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: opacity 0.3s ease-in-out;
    }

    &--hidden {
      opacity: 0;
      // visibility: hidden;
    }
  }
}

.about__video-btn--hidden {
  &:hover {
    opacity: 1;
  }
}

.about__video-pause {
  opacity: 0;
  visibility: hidden;
}

.video--play {
  .about__video-pause {
    opacity: 1;
    visibility: visible;
  }
}

.about-content {
  width: 480px;
  padding-top: 60px;

  &__subtitle {
    margin-bottom: 8px;
  }

  &__title {
    margin-bottom: 32px;
  }

  &__text {
    margin-bottom: 38px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: var(--text);
  }

  &__list {
    margin-bottom: 43px;
  }

  &__list-item {
    background-image: url("../img/check.svg");

    font-family: var(--font-family);
    font-weight: 600;
    font-size: 16px;
    line-height: 140%;
    color: #ccc;
    padding-left: 34px;

    &:not(:last-child) {
      margin-bottom: 13px;
    }
  }

  &__info {
  }
}

.info-numb {
  &__item {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: 50%;
      transform: translateY(-50%);
      right: 37px;
      width: 1px;
      height: 20px;
      background-color: var(--white);
      opacity: 0.2;
    }

    &:not(:last-child) {
      padding-right: 77px;
    }
  }

  &__number {
    display: block;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 35px;
    line-height: 132%;
    color: var(--white);
  }

  &__desr {
    display: block;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 11px;
    line-height: 140%;
    color: var(--gray);
  }
}

.value {
  padding-bottom: 83px;

  &__subtitle {
    text-align: right;
    margin-bottom: 6px;
    margin-right: 376px;
  }

  &__inner {
  }

  &__left {
    max-width: 572px;
    margin-right: 130px;
  }

  &__left-item {
    &:not(:last-child) {
      margin-bottom: 49px;
    }
  }

  &__left-num {
    margin-right: 53px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 60px;
    line-height: 135%;
    color: var(--white);
  }

  &__left-descr {
  }

  &__left-title {
    display: block;
    margin-bottom: 23px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 135%;
    color: var(--white);
  }

  &__left-text {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #aaa;
  }

  &__right {
  }
}

.value-content {
  max-width: 455px;

  &__title {
    margin-bottom: 32px;
  }

  &__text {
    margin-bottom: 38px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: var(--text);
  }

  &__links {
    padding-left: 50px;
  }

  &__link {
    &:not(:last-child) {
      margin-right: 31px;
      margin-left: -13px;
    }

    span {
      width: 51px;
      height: 51px;
      margin-right: 15px;
      border-radius: 100%;
      background-color: var(--accent);

      img {
        height: 16px;
        object-fit: cover;
      }
    }
  }

  &__link-video {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    letter-spacing: 0.18em;
    color: var(--white);
  }
}
