  will-change: max-height;
  overflow: hidden;
  transition: 0.3s linear;
  box-sizing: content-box;
}
.accardeon__img {
  position: absolute;
  max-width: 250px;
  max-height: 280px;
  object-fit: cover;
  top: -77px;
  right: 22px;
  transform: rotate(15deg);
  z-index: -1;
  opacity: 0;
  transition: opacity 0.1s linear, right 0.3s linear, top 0.3s linear;
}

.accardeon__item--open .accardeon__box::before {
  transform: rotate(180deg);
}

.accardeon__item--open .accardeon__box::after {
  opacity: 0;
}

