body,
html {
  height: 100%;
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  position: relative;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  color: var(--white);
  background-color: var(--bg);
}

//,  ,       
.hidden {
  overflow: hidden;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100%;
  z-index: 5;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: 0;
    z-index: -1;
    width: 340px;
    border: 1px solid var(--white);
    height: 102%;
    opacity: 0.06;
  }

  &::before {
    left: 442px;
  }

  &::after {
    right: 442px;
  }
}

.main {
  flex-grow: 1;
}

.container-page {
  max-width: 1204px;
  padding: 0 15px;
  margin: 0 auto;
}

.title-section {
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 40px;
  line-height: 135%;
  color: var(--white);
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.link-circle {
  position: relative;
  display: inline-block;
  padding-top: 10px;
  transform: translateX(-15px);
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.18em;
  color: var(--white);
  z-index: 1;
  transition: color 0.3s ease-in-out;

  &::before {
    position: absolute;
    content: "";
    top: -10px;
    left: -24px;
    width: 60px;
    height: 60px;
    border-radius: 100%;
    background-color: #333333;
    z-index: -1;
    transition: background-color 0.3s ease-in-out;
  }

  &:hover {
    color: var(--accent);

    &::before {
      background-color: var(--gray);
    }
  }
}

.subtitle {
  display: block;
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 14px;
  line-height: 160%;
  letter-spacing: 0.15em;
  color: var(--gray);
}
