.hero {
  position: relative;
  padding-top: 174px;
  padding-bottom: 70px;
  min-height: 100vh;

  &__text {
    position: relative;
    max-width: 417px;
    margin-left: 118px;
    margin-bottom: 107px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    color: #ccc;
    padding-left: 17px;

    &::before {
      position: absolute;
      content: "";
      top: 50%;
      transform: translateY(-50%);
      left: 1px;
      width: 3px;
      height: 83%;
      background-color: var(--accent);
    }
  }
}

.title {
  position: relative;
  margin-bottom: 77px;
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 100px;
  line-height: 130%;
  text-align: center;
  color: var(--white);

  &__word {
    position: absolute;
    bottom: -197px;
    right: 19px;
    color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: var(--white);
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 142px;
    line-height: 140%;
    text-align: center;
  }
}

.hero-bottom {
  &__slider {
  }

  &__right {
    max-width: 434px;
    width: 100%;
  }

  &__link {
    margin-bottom: 115px;

    &::before {
      background-color: var(--accent);
    }
  }

  &__peoples {
  }
}

.hero-slider {
  position: relative;
  z-index: 1;

  &::before {
    position: absolute;
    content: "";
    top: 50%;
    transform: translateY(-50%);
    left: -79px;
    width: 307px;
    height: 307px;
    background-color: var(--accent);
    border-radius: 100%;
    z-index: -1;
  }

  &__img {
    width: 470px;
    height: 230px;
  }
}

.peoples {
  &__images {
    margin-right: 19px;
  }

  &__img {
    width: 80px;
    height: 80px;
    z-index: 3;

    &:nth-child(2) {
      margin-left: -20px;
      z-index: 2;
    }

    &:nth-child(3) {
      margin-left: -20px;
      z-index: 1;
    }

    img {
      object-fit: cover;
    }
  }

  &__descr {
  }

  &__subtitle {
    display: block;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 140%;
    color: var(--white);
  }

  &__text {
    display: block;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: #ccc;
  }
}

.scroll-btn {
  position: absolute;
  left: 50%;
  bottom: -30px;
  padding: 19px 27px;
  background-color: var(--accent);
  border-radius: 50px;
  transform: translateX(-50%) rotate(90deg);
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.25em;
  text-decoration: none;
}
