@media (max-width: 1400px) {}

@media (max-width: 1200px) {
  .menu {
    margin-left: auto;
    margin-right: auto;
  }

  .title__word {
    position: relative;
    left: 0;
    bottom: 0;
  }

  .partners {
    padding-top: 120px;
  }

  .partners__inner {
    flex-direction: column;
  }

  .partners__inner::before {
    top: -96px;
    left: 10%;
  }

  .partners__text {
    max-width: 100%;
    padding-left: 17px;
    margin-right: 0;
    margin-bottom: 40px;
  }

  .partners-list {
    gap: 50px;
    flex-wrap: wrap;
    justify-content: center;
  }

  .partners-list__item:not(:last-child) {
    margin-right: 0;
  }

  .steps-list {
    flex-wrap: wrap;
    justify-content: center;
  }

  .features__top {
    flex-direction: column;
    align-items: center;
  }

  .features__top-left {
    margin-bottom: 30px;
  }

  .features__top::before {
    bottom: -65px;
    right: 0;
  }

  .portfolio__inner {
    flex-direction: column;
    gap: 50px;
    align-items: center;
  }

  .portfolio-content {
    max-width: 720px;
  }

  .quote__inner {
    flex-direction: column;
    align-items: center;
  }

  .quote__image {
    margin-right: 0;
  }

  .blog-list__item {
    grid-column: 4 / 10;
  }

  .blog-list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .contact__inner {
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }

  .contact-content {
    max-width: 700px;
    width: 100%;
    order: -1;
  }

  .about__inner {
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }

  .about__left {
    margin-right: 0;
    order: 1;
  }

  .value__inner {
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }

  .value__left {
    max-width: 100%;
    margin-right: 0;
  }

  .value-content {
    max-width: 800px;
  }
}

@media (max-width: 1024px) {
  .hero {
    padding-top: 110px;
  }

  .title {
    font-size: 64px;
  }

  .title__word {
    font-size: 80px;
    line-height: 100%;
  }

  .hero-bottom {
    flex-direction: column;
    align-items: center;
  }

  .hero-bottom__link {
    margin-bottom: 50px;
  }

  .hero-bottom__slider {
    margin-bottom: 60px;
  }

  .blog__top {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 992px) {
  .burger {
    pointer-events: auto;
  }
  .box-burger {
    z-index: 4;
  }

  .header__logo {
    z-index: 4;
  }

  .menu__list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(-120%);
    transition: transform .5s ease-in-out;
    background-color: rgba($color: #181818, $alpha: 1);
    width: 100%;
    min-height: 100vh;
    gap: 40px;
  }

  .menu__item:not(:last-child) {
    margin-right: 0;
  }

  .menu__link {
    font-size: 24px;
  }

  .header__inner {
    justify-content: space-between;
  }

  .menu--active {
    .menu__list {
      transform: translateX(0);
    }
  }

  .header {
    padding-top: 20px;
  }

  .contact__top {
    margin-bottom: 50px;
    flex-direction: column;
    gap: 40px;
  }

  .contact__title {
    width: 100%;
  }

  .with-us {
    width: auto;
    height: auto;
    align-self: flex-end;
    background-color: transparent;
    color: var(--accent);

    span {
      padding-top: 0;
    }
  }

  .about__image {
    display: none;
  }

  .about__video {
    position: relative;
    bottom: 0;
    right: 0;
  }

  .about__left {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .about__left::after {
    display: none;
  }

  .about {
    padding-bottom: 80px;
  }

  .about-content {
    padding-top: 0;
  }

  .footer__bottom {
    flex-wrap: wrap;
    gap: 20px;
  }

  .footer__inner {
    flex-wrap: wrap;
  }

  .social {
    margin-bottom: 20px;
    padding-top: 0;
  }
}

@media (max-width: 768px) {
  .title {
    font-size: 42px;
    margin-bottom: 40px;
  }

  .hero__text {
    margin-left: 0;
    margin-bottom: 70px;
  }

  .title__word {
    font-size: 70px;
  }

  .hero-slider__img {
    width: 300px;
    height: auto;
  }

  .link-circle {
    transform: none;
  }

  .hero-bottom__slider {
    margin-bottom: 40px;
  }

  .hero__text {
    margin-bottom: 40px;
    font-size: 16px;
  }

  .peoples__img {
    width: 60px;
    height: 60px;
  }

  .peoples__img {
    font-size: 16px;
    line-height: 1;
  }

  .peoples__text {
    font-size: 14px;
  }

  .hero-bottom__link {
    margin-left: 40px;
  }

  .hero-slider::before {
    display: none;
  }

  .steps-list__item:nth-child(2),
  .steps-list__item:nth-child(3) {
    transform: none;
  }

  .features__title {
    font-size: 28px;
    line-height: 120%;
  }

  .steps-list__image {
    width: 300px;
    height: auto;
    filter: grayscale(0);
  }

  .steps-list__image::before {
    opacity: 1;
  }

  .steps-list {
    gap: 40px;
  }

  .steps-list__title {
    font-size: 26px;
  }

  .features {
    padding-bottom: 80px;
  }

  .accardeon__item--open .accardeon__img {
    display: none;
  }

  .blog-post__image {
    clip-path: polygon(0 0, 100% 0, 100% 77%, 0% 77%, 0% 100%, 0 100%);
    height: 260px;
  }

  .blog-post__content {
    margin-top: -60px;
  }

  .blog-post__title {
    font-size: 24px;
    margin-bottom: 15px;
  }

  .blog-post__title {
    margin-bottom: 14px;
  }

  .blog__top-left {
    width: 100%;
    margin-bottom: 30px;
  }

  .blog__top-right {
    width: 100%;
  }
}

@media (max-width: 576px) {
  .title-section {
    font-size: 28px;
    line-height: 120%;
  }

  .accardeon__num {
    display: none;
  }

  .scroll-btn {
    padding: 13px 20px;
    font-size: 12px;
    line-height: 1;
    letter-spacing: 3px;
  }

  .quote__image {
    width: 300px;
    height: auto;
  }

  .quote__image::after {
    display: none;
  }

  .quote__text::before {
    top: -35px;
  }

  .quote__text {
    font-size: 20px;
    line-height: 140%;
  }

  .quote {
    padding-bottom: 80px;
  }

  .accardeon__title {
    padding-bottom: 15px;
    font-size: 24px;
    display: flex;
    flex-direction: column;
    align-items: start;
  }

  .portfolio {
    padding-bottom: 80px;
  }

  .portfolio__inner::before {
    left: 10%;
    bottom: -100px;
  }

  .contact__top {
    gap: 20px;
    margin-bottom: 20px;
  }

  .contact-content__title {
    margin-bottom: 20px;
  }
  .features__top-left {
    margin-bottom: 20px;
    max-width: 100%;
    margin-right: auto;
  }
  .contact-content__text {
    margin-bottom: 20px;
  }

  .link-circle {
    font-size: 12px;
  }

  .link-circle::before {
    top: -7px;
    left: -22px;
    width: 50px;
    height: 50px;
  }

  .contact {
    padding-bottom: 80px;
  }

  .contact-content::before {
    bottom: -12px;
    right: -10px;
  }

  .with-us {
    margin-right: 0;
  }

  .with-us__link {
    font-size: 12px;
    margin-bottom: 0;
  }

  .with-us__link+span {
    display: none;
  }

  .subtitle {
    font-size: 12px;
  }

  .about-content {
    width: 100%;
  }

  .about-content__title {
    margin-bottom: 20px;
  }

  .about-content__text {
    margin-bottom: 20px;
  }

  .info-numb__number {
    font-size: 24px;
    line-height: 120%;
    text-align: center;
    margin-bottom: 7px;
  }

  .about-content__list {
    margin-bottom: 30px;
  }

  .about__video {
    width: 280px;
    height: 300px;
  }

  .about__video-btn {
    width: 65px;
    height: 65px;
  }

  .about-content__info {
    justify-content: center;
  }

  .info-numb__item:not(:last-child) {
    padding-right: 20px;
  }

  .info-numb__item::before {
    display: none;
  }

  .value__left-title {
    margin-bottom: 10px;
    font-size: 22px;
    line-height: 120%;
  }

  .value__left-num {
    margin-right: 25px;
    font-size: 26px;
    line-height: 120%;
  }

  .value__subtitle {
    text-align: left;
    margin-bottom: 0;
    margin-right: 0;
  }

  .value-content__title {
    margin-bottom: 20px;
  }

  .value {
    padding-bottom: 80px;
  }

  .value-content__links {
    padding-left: 0;
  }

  .value-content__link:not(:last-child) {
    margin-right: 10px;
    margin-left: 27px;
  }

  .value-content__link span {
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }

  .value-content__link-video {
    font-size: 14px;
    line-height: 120%;
  }

  .value__left-item:not(:last-child) {
    margin-bottom: 30px;
  }

  .steps-list__image {
    width: 240px;
    max-height: 260px;
  }

  .steps-list__title {
    font-size: 20px;
  }

  .accardeon__content {
    font-size: 14px;
  }

  .blog-post {
    align-items: center;
  }

  .blog-post__title {
    width: 100%;
  }

  .blog-post__image {
    max-height: 220px;
  }

  .blog-post__image {
    clip-path: none;
  }

  .blog-post__content {
    margin-top: 0;
  }

  .footer__bottom-links {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .social__item:not(:last-child) {
    padding-right: 7px;
    margin-right: 7px;
  }
  .blog__top-left {
    // margin-bottom: 15px;
  }
  .social__link {
    font-size: 16px;
    line-height: 130%;
  }

  .footer__inner {
    gap: 20px;
    padding-bottom: 30px;
  }

  .footer__link {
    margin-bottom: 0;
  }

  .footer {
    padding-top: 0;
  }

  .footer__text {
    max-width: 100%;
    margin-bottom: 20px;
    font-size: 14px;
  }

  .footer__item-title {
    margin-bottom: 10px;
  }

  .footer__social-descr {
    font-size: 22px;
    line-height: 130%;
  }

  .footer__bottom {
    padding: 15px 0 20px;
  }
}
