.quote {
  padding-bottom: 150px;

  &__inner {
  }

  &__image {
    position: relative;
    margin-right: 100px;
    width: 370px;
    height: 430px;

    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      top: 20px;
      left: 20px;
      border: 1px solid var(--white);
      opacity: 0.2;
      z-index: 1;
    }

    &::after {
      position: absolute;
      content: "";
      bottom: -37px;
      right: -41px;
      background-image: url("../img/shap-quote.svg");
      width: 88px;
      height: 88px;
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: grayscale(1);
  }

  &__right {
    position: relative;
    max-width: 700px;
    padding-top: 59px;

    &::before {
      position: absolute;
      content: "";
      bottom: 19px;
      right: -55px;
      background-image: url("../img/shape-two.svg");
      width: 106px;
      height: 76px;
    }
  }

  &__text {
    position: relative;
    margin-bottom: 34px;
    font-family: var(--font-family);
    font-style: italic;
    font-weight: 500;
    font-size: 24px;
    line-height: 160%;
    color: var(--white);
    &::before {
      position: absolute;
      content: "";
      top: -59px;
      left: 0;
      background-image: url("../img/quote.svg");
      width: 42px;
      height: 30px;
      background-repeat: no-repeat;
      background-position: auto;
    }
  }

  &__author-img {
    width: 70px;
    height: 70px;
    border-radius: 100%;
    overflow: hidden;
  }

  &__author-descr {
    margin-left: 14px;

    span {
      display: block;
    }
  }

  &__author-name {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 160%;
    color: var(--white);
  }

  &__author-prof {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 160%;
    color: #ff7425;
  }
}
